@import url("./fonts/fonts.scss");
html::after {
  background: #eee;
  bottom: 0;
  color: #000;
  font-family: monospace, monospace;
  font-size: 10px;
  font-weight: normal;
  line-height: 1.5;
  padding: 5px 10px;
  position: fixed;
  right: 0;
  z-index: 9999;
}
